import React from 'react'
import UsersNavbar from './UsersNav';
import { Link } from 'react-router-dom';
import '../Styles/mypf.css';
import SocialSphere from '../Components/SocialSphere.png';
import { useSelector } from 'react-redux';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import CommentIcon from '@mui/icons-material/Comment';

function UsersProfile() {
  const visitorProfile = useSelector(state => state.visitProfileDetails); 
  const visitorPosts = useSelector(state => state.visitProfilePosts);
  console.log(visitorPosts);
  return (
    <div>
      <UsersNavbar />
      <div>
        <h1>My Profile</h1>
        <div  class="profile-container">
          <div class="profile-card">
            <div className='pfpd'>
            <img src={visitorProfile.profilePic ? `data:image/jpeg;base64,${visitorProfile.profilePic}` : SocialSphere } alt="ProfilePhoto" width="100px" height="100px" />
            <div class="bio">
              <b>Bio: </b><span>{visitorProfile.bio}</span>
            </div>
            </div>
            <div class="row">
              <div class="col">
                <b>Email: </b>
                <p>{visitorProfile.email}</p>
                <b>LinkedIn: </b>
                <p><Link to="{userDetails.linkdIn}">{visitorProfile.linkdIn}</Link></p>
                <b>GitHub: </b>
                <p><Link to="{userDetails.gitHub}">{visitorProfile.gitHub}</Link></p>
                <b>Date Of Birth: </b>
                <p>{visitorProfile.dob}</p>
              </div>
              <div class="col">
                <b>Current City: </b>
                <p>{visitorProfile.currentCity}</p>
                <b>College: </b>
                <p>{visitorProfile.college}</p>
                <b>Gender: </b>
                <p>{visitorProfile.gender}</p>
              </div>
            </div>
          </div>
        </div>
      <div>
      <h1 style={{textAlign:"center",color:"black"}}>MyPosts</h1>
      <div className='mainPosts'>
        {
          visitorPosts.map(post => {
            return <div className="allPosts-details">
            <div  className="post-card">
              <div className="post-header">
                <img src={post.user.profilePic ? `data:image/jpeg;base64,${post.user.profilePic}` : SocialSphere }  alt="ProfilePhoto" class="profile-photo" /> 
                <span className="username"  style={{color: "orange",marginRight:"10px"}}>{post.user.username}</span>  
              </div>
              <div className="post-content">
                <p>
                  <strong>Caption: </strong><span>{post.caption}</span>
                </p>
                <img src={`data:image/jpeg;base64,${post.photo}`}  alt="PostPhoto" class="post-image" />
              </div>
              <div className="post-footer">
                <form action="/likePost" className="like-form">
                  <input name="id" type="hidden" />
                  <FavoriteBorderIcon style={{cursor:"pointer"}} />
                  <span className="likes-count">{post.likes}</span>
                </form>
                <form action="/addComment" method="post" className="comment-form">
                  <input name="id" type="hidden" />
                  <input type="text" className="comment-input" name="comment"
                    placeholder="Add Comments...." />
                  <input type="submit" value="Comment" className="btn btn-comment" />
                </form>
              </div>
              <div className="comments-section">
                <span>Comments:</span>
                <span><CommentIcon style={{color:"yellowgreen"}} /></span>
                <ul>
                  <li className="comment-item">
                    <p>Welcome to Social Sphere Welcome to Social Sphere</p>
                  </li>
                  <li className="comment-item">
                    <p>Welcome to Social Sphere</p>
                  </li>
                  <li className="comment-item">
                    <p>Welcome to Social Sphere Welcome to Social Sphere Welcome to Social Sphere</p>
                  </li>
                  <li className="comment-item">
                    <p>Welcome to Social Sphere</p>
                  </li>
                  <li className="comment-item">
                    <p>Welcome to Social Sphere Welcome to Social Sphere</p>
                  </li>
                  <li className="comment-item">
                    <p>Welcome to Social Sphere</p>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          })
        }
      </div>
    </div>


      </div>
    </div>
  );
}

export default UsersProfile;

