import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { fetchAllPosts, fetchComments, fetchLikes, fetchUserDetails, fetchUserPosts } from '../UsersStore/userActions';
import SocialSphere from '../Components/SocialSphere.png';
import '../Styles/UsersHome.css';
import CommentIcon from '@mui/icons-material/Comment';
import { Link, useNavigate } from 'react-router-dom';
import { IconButton } from '@mui/material';
import FavoriteSharpIcon from '@mui/icons-material/FavoriteSharp';


function UserPosts() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const userPosts = useSelector((state) => state.userPosts);
  const userDetails = useSelector((state) => state.userDetails);
  useEffect(() => {
    const username = localStorage.getItem("Username") || '';
    if (username) {
      dispatch(fetchUserDetails(username));
      dispatch(fetchUserPosts(username));
    }
    dispatch(fetchAllPosts());
  }, [dispatch]);
  const [liked, setLiked] = useState(false);
  const handleLike = (event) => {
    event.preventDefault();
    const form = event.target;
    const id = form.id.value;
    const likedProfileName = event.target.Username.value;
    setLiked(true);
    dispatch(fetchLikes(id, likedProfileName));
    window.location.reload();
    navigate('/usersHome');
  };
  const handleComments = (event) => {
    event.preventDefault();
    const id = event.target.id.value;
    const commentedProfileName = event.target.CommentedUsername.value;
    const comment = event.target.comment.value;
    dispatch(fetchComments(id, commentedProfileName, comment));
    navigate('/usersHome');
    window.location.reload();
  };
  
  return (
    <div>
      <h1 style={{ textAlign: "center", color: "black" }}>MyPosts</h1>
      <div className='mainPosts'>
        {
          userPosts.map(post => {
            return <div className="allPosts-details">
              <div className="post-card">
                <div className="post-header">
                  <img src={post.user.profilePic ? `data:image/jpeg;base64,${post.user.profilePic}` : SocialSphere} alt="ProfilePhoto" class="profile-photo" />
                  <span className="username" style={{ color: "orange", marginRight: "10px" }}>{post.user.username}
                    <Link to={'/mypf'} style={{ fontSize: "16px" }} id="visitPf" >VisitProfile</Link></span>
                </div>
                <div className="post-content">
                  <p>
                    <strong>Caption: </strong><span>{post.caption}</span>
                  </p>
                  <img src={`data:image/jpeg;base64,${post.photo}`} alt="PostPhoto" class="post-image" />
                </div>
                <div className="post-footer">
                  <form onSubmit={handleLike} className="like-form">
                    <input name="id" type="hidden" value={post.id} />
                    <input name="Username" type='hidden' value={userDetails.username} />
                    <IconButton type='submit' style={{ color: "red" }}>
                      {liked ? (
                        <FavoriteSharpIcon style={{ color: 'red', cursor: 'pointer' }} />
                      ) : (
                        <FavoriteSharpIcon style={{ cursor: 'pointer' }} />
                      )}<span className="likes-count" style={{ color: "black" }}>{post.likes}</span>
                    </IconButton>
                  </form>
                  <form onSubmit={handleComments} className="comment-form">
                    <input name="id" type="hidden" value={post.id} />
                    <input name="CommentedUsername" type='hidden' value={userDetails.username} />
                    <input type="text" className="comment-input" name="comment"
                      placeholder="Add Comments...." />
                    <input type="submit" value="Comment" className="btn btn-comment" />
                  </form>
                </div>
                <div className="comments-section">
                  <span>Comments:</span>
                  <span><CommentIcon style={{ color: "yellowgreen" }} /></span>
                  <div>
                      <div>
                        {post.comments && post.comments.length > 0 ? (
                          <ul>
                            {post.comments.map((comment, ind) => (
                              <li className="comment-item" key={ind}>
                                {comment}
                              </li>
                            ))}
                          </ul>
                        ) : (
                          <ul>
                            <li className="comment-item">
                              {post.comment || "No comments available."}
                            </li>
                          </ul>
                        )}
                      </div>
                  </div>
                </div>
              </div>
            </div>
          })
        }
      </div>
    </div>
  )
}

export default UserPosts;


