import React, { useEffect, useState } from 'react';
import {
  Box,
  Button,
  TextField,
  RadioGroup,
  FormControlLabel,
  FormLabel,
  Typography,
  IconButton,
  Radio,
  Container
} from '@mui/material';
import { PhotoCamera } from '@mui/icons-material';
import UsersNavbar from './UsersNav';
import SocialSphere from '../Components/SocialSphere.png';
import '../Styles/updatepf.css';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { fetchUserDetails } from '../UsersStore/userActions';

const UpdateProfile = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    Username:localStorage.getItem("Username")|| '',
    profilePic: null,
    username: '',
    email: '',
    password: '',
    dob: '',
    gender: '',
    currentCity: '',
    college: '',
    bio: '',
    linkdIn: '',
    gitHub: '',
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setFormData((prevData) => ({
      ...prevData,
      profilePic: file,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('http://localhost:8081/updateProfile', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      console.log('Response:', response.data);
      if(response.data === "Profile Updated"){
        Swal.fire("Profile Updated Successfully","Profile Updated Successfully!","success");
        navigate('/usersHome');
      }else{
        Swal.fire("Error While Updation","Try Again!","error");
        navigate("/upf");
      }
    } catch (error) {
      console.error('Error uploading:', error);
      Swal.fire("Error While Updation","Try Again!","error");
        navigate("/upf");
    }
  };

  const dispatch = useDispatch();
    const userDetails = useSelector((state) => state.userDetails);

    useEffect(() => {
        const username = localStorage.getItem("Username") || '';
        if (username) {
            dispatch(fetchUserDetails(username));
        }
    }, [dispatch]);

  return (
    <div>
      <UsersNavbar />
      <div style={{ backgroundColor: "#f8f9fa", padding: "20px" }}>
        <Container className='profile-edit-form' >
          <Box sx={{ p: 3 }} >
            <Typography variant="h4" align="center" sx={{ color: 'orange', mb: 4 }}>
              Edit Profile
            </Typography>
            <form onSubmit={handleSubmit}>
              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mb: 4 }}>
                <input
                  accept="image/*"
                  style={{ display: 'none' }}
                  id="profile-pic-upload"
                  type="file"
                  name='profilePic'
                  onChange={handleFileChange}
                />
                <label htmlFor="profile-pic-upload">
                  <IconButton color="primary" component="span">
                    <PhotoCamera />
                  </IconButton>
                </label>
                  <img src={userDetails.profilePic ? `data:image/jpeg;base64,${userDetails.profilePic}` : SocialSphere } alt="Profile" style={{ borderRadius: '50%', width: '100px', height: '100px', objectFit: 'cover' }} />
              </Box>

              <TextField
                label="Username"
                variant="outlined"
                fullWidth
                margin="normal"
                required
                name='username'
                value={formData.username}
                onChange={handleChange}
              />
              <TextField
                label="Email"
                variant="outlined"
                fullWidth
                margin="normal"
                required
                name='email'
                type="email"
                value={formData.email}
                onChange={handleChange}
              />
              <TextField
                label="Password"
                variant="outlined"
                fullWidth
                margin="normal"
                required
                type="password"
                name='password'
                value={formData.password}
                onChange={handleChange}
              />
              <TextField
                label="Date of Birth"
                variant="outlined"
                fullWidth
                margin="normal"
                required
                name='dob'
                type="date"
                InputLabelProps={{ shrink: true }}
                value={formData.dob}
                onChange={handleChange}
              />
              <FormLabel component="legend">Gender</FormLabel>
              <RadioGroup
                row
                value={formData.gender}
                name='gender'
                onChange={handleChange}
              >
                <FormControlLabel className='gender-label' value="Male" control={<Radio />} label="Male" />
                <FormControlLabel className='gender-label' value="Female" control={<Radio />} label="Female" />
                <FormControlLabel className='gender-label' value="Others" control={<Radio />} label="Others" />
              </RadioGroup>
              <TextField
                label="Current City"
                variant="outlined"
                fullWidth
                margin="normal"
                required
                name='currentCity'
                value={formData.currentCity}
                onChange={handleChange}
              />
              <TextField
                label="College"
                variant="outlined"
                fullWidth
                margin="normal"
                name='college'
                required
                value={formData.college}
                onChange={handleChange}
              />
              <TextField
                label="Bio"
                variant="outlined"
                fullWidth
                margin="normal"
                required
                name='bio'
                value={formData.bio}
                onChange={handleChange}
              />
              <TextField
                label="LinkedIn"
                variant="outlined"
                fullWidth
                margin="normal"
                name='linkdIn'
                value={formData.linkdIn}
                required
                onChange={handleChange}
              />
              <TextField
                label="GitHub"
                variant="outlined"
                fullWidth
                name='gitHub'
                margin="normal"
                required
                value={formData.gitHub}
                onChange={handleChange}
              />
              <br></br>
              <br></br>
              <Button type="submit" variant="contained" color="primary" fullWidth className='btn-update'>
                Update Profile
              </Button>
            </form>
          </Box>
        </Container>
      </div>
    </div>
  );
};

export default UpdateProfile;
