import React from 'react';
import {
  Container,
  TextField,
  Button,
  Typography,
  Grid,

} from '@mui/material';

import '../Styles/signup.css';
import Swal from 'sweetalert2';
import { useDispatch} from 'react-redux';
import { updatePassword } from '../UsersStore/userActions';


const PwdResetPage = () => {
  const dispatch = useDispatch();
  const handleSubmit = (event) => {
    event.preventDefault();
    const email = event.target.email.value;
    const newPassword = event.target.newPassword.value;
    const confirmPassword = event.target.confirmPassword.value;
    
    let password = '';

    if(newPassword === confirmPassword){
      password = newPassword;
      Swal.fire("Password  Matched", "Password reset Success", "success");
    }else{
      Swal.fire("Please match the above password", "Please Match Password", "error");
    }
    dispatch(updatePassword(email, password));
  };

  return (
    <Container maxWidth="sm" style={{ marginTop: '2rem',backgroundColor:"#f8f9fa",padding:"10px",borderRadius:"10px" }}>
      <div className="signup-form">
        <Typography variant="h4" align="center" gutterBottom color='#1abc9c'>
          Social Sphere
        </Typography>
        <hr />
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                label="Email"
                name="email"
                type="email"
                variant="outlined"
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="NewPassword"
                name="newPassword"
                type="password"
                variant="outlined"
                fullWidth
                required
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                label="ConfirmPassword"
                name="confirmPassword"
                type="password"
                variant="outlined"
                fullWidth
                required
              />
            </Grid>
            
            <Grid item xs={12}>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
              >
                UpdatePassword
              </Button>
            </Grid>
            
          </Grid>
        </form>
      </div>
    </Container>
  );
};

export default PwdResetPage;
