import React, { useState } from 'react';
import {
  Container,
  TextField,
  Button,
  Typography,
  Grid
} from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import '../Styles/Login.css';
import axios from 'axios';
import Swal from 'sweetalert2';


const Log = () => {

  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    username: '',
    password: '',
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (event) => {
   
    event.preventDefault();

    try {
      const response = await axios.post('http://localhost:8081/login', formData);
      console.log(response.data); 
      if(response.data === "Login Success"){
        Swal.fire('Login Success', 'Welcome User', 'success');
        localStorage.setItem("Username", formData.username);
        localStorage.setItem("isLoggedIn", true);
        navigate('/usersHome');
        window.location.reload();
      }else if(response.data === "Invalid Credentials"){
        Swal.fire('Login Failed', 'Invalid Username or Password', 'error');
        navigate('/');
      }else if(response.data === "Login Fail and User Not Found"){
        Swal.fire('Login Failed', 'User Not Found in our DataBase', 'error');
        navigate('/');
      }else{
        navigate("/");
      }
    } catch (error) {
      console.error('Error during signup:', error); 
    }  
  };

  return (
    
    <Container maxWidth="md" style={{ marginTop: '2rem',backgroundColor:"#f8f9fa",padding:"20px",borderRadius:"10px"}}>
      <div className="login-form">
        <Typography variant="h4" align="center" gutterBottom color='#1abc9c'>
          Welcome to SocialSphere
        </Typography>
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                label="Username"
                name="username"
                variant="outlined"
                fullWidth
                required
                value={formData.username}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Password"
                name="password"
                type="password"
                variant="outlined"
                fullWidth
                required
                value={formData.password}
                onChange={handleChange}
              />
            </Grid>
            
            <Grid item xs={12}>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
              >
                LOGIN
              </Button>
            </Grid>
            <Grid item xs={12}>
              <hr />
              <div className="text-center">
                <Link to="/resetPwdLink" style={{ marginRight: '10px' }}>
                  Forgot Password?
                </Link>
                <Link to="/SignUpPage">Create New Account</Link>
              </div>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
};

export default Log;



