import React from "react";
import { BrowserRouter, Link, Route,   Routes} from "react-router-dom";
import Home from "./Components/Home";
import LoginForm from "./Components/LoginPage";
import SignupForm from "./Components/SignUpPage";
import PwdResetForm from "./Components/ForgotPwd";
import PwdResetPage from "./Components/ForgotPwdValid";
import UsersHome from "./UsersComponents/Home";
import CreatePost from "./UsersComponents/CreatePost";
import MyProfile from "./UsersComponents/MyProfile";
import MyPosts from "./UsersComponents/MyPosts";
import UpdateProfile from "./UsersComponents/UpdateProfile";
import UsersProfile from "./UsersComponents/UsersProfile";
import Logout from "./UsersComponents/Logout";


function App() {
  const isLoggedIn = localStorage.getItem("isLoggedIn") || true;
  return (
      <BrowserRouter>
        <Routes>
            <Route index element = {<Home />} />
            <Route  path="/SignUpPage" element = {<SignupForm />} />
            <Route path="/loginPage"element = {<LoginForm />} />
            <Route path="/resetPwdLink"element = {<PwdResetForm />} />
            <Route path="/resetPwdForm/:token"element = {<PwdResetPage />} />
            <Route path="/usersHome"element = {isLoggedIn === "true" ? <UsersHome />: <Link to={'/'} style={{textAlign:"center",textDecoration:"none",color:"black"}}>Please Login <span style={{textDecoration:"underline",textAlign:"center",color:"blue"}}> to continue with SocialSphere.</span></Link>} />
            <Route path="/newPost"element = {isLoggedIn === "true" ? <CreatePost /> : <Link to={'/'} style={{textAlign:"center",textDecoration:"none",color:"black"}}>Please Login <span style={{textDecoration:"underline",textAlign:"center",color:"blue"}}> to continue with SocialSphere.</span></Link> } />
            <Route path="/mypf"element = {isLoggedIn === "true" ?<MyProfile /> : <Link to={'/'} style={{textAlign:"center",textDecoration:"none",color:"black"}}>Please Login <span style={{textDecoration:"underline",textAlign:"center",color:"blue"}}> to continue with SocialSphere.</span></Link>} />
            <Route path="/myps"element = {isLoggedIn === "true" ? <MyPosts /> : <Link to={'/'} style={{textAlign:"center",textDecoration:"none",color:"black"}}>Please Login <span style={{textDecoration:"underline",textAlign:"center",color:"blue"}}> to continue with SocialSphere.</span></Link>} />
            <Route path="/upf"element = {isLoggedIn === "true" ? <UpdateProfile /> : <Link to={'/'} style={{textAlign:"center",textDecoration:"none",color:"black"}}>Please Login <span style={{textDecoration:"underline",textAlign:"center",color:"blue"}}> to continue with SocialSphere.</span></Link>} />
            <Route path="/vspf" element={isLoggedIn === "true" ? <UsersProfile />: <Link to={'/'} style={{textAlign:"center",textDecoration:"none",color:"black"}}>Please Login <span style={{textDecoration:"underline",textAlign:"center",color:"blue"}}> to continue with SocialSphere.</span></Link>}  />
            <Route path="/logout" element={<Logout />}  />
        </Routes>
      </BrowserRouter>  
  );
}

export default App;




