import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, Box, IconButton, Menu, MenuItem } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import '../Styles/NavbarStyles.css';
import SocialSphere from './SocialSphere.png';

const pages = [
  { name: "Home", id: "homePage", path: "/" },
  { name: "Login", id: "loginPage", path: "/loginPage" },
  { name: "Signup", id: "signUpPage", path: "/signUpPage" },
];

const Navbar = () => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar position="static" sx={{ backgroundColor: '#1a1a4b' }}>
      <Toolbar>
      <img src={SocialSphere} alt="Logo" style={{ height: '50px',width:"50px", marginRight: '16px',borderRadius:"80%" }} />
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          SocialSphere
        </Typography>
        <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
          {pages.map((page) => (
            <NavLink
              key={page.id}
              to={page.path}
              className={({ isActive }) => (isActive ? 'activatedNav' : 'notActivatedNav')}
            >
              <Button color="inherit">{page.name}</Button>
            </NavLink>
          ))}
        </Box>
        <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
          <IconButton color="inherit" onClick={handleMenuClick}>
            <MenuIcon />
          </IconButton>
          <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose}>
            {pages.map((page) => (
              <MenuItem key={page.id} onClick={handleClose}>
                <NavLink
                  to={page.path}
                  className={({ isActive }) => (isActive ? 'activatedNav' : 'mobnotActivatedNav')}
                  
                >
                  {page.name}
                </NavLink>
              </MenuItem>
            ))}
          </Menu>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
