import React from 'react';
import {
  Container,
  TextField,
  Button,
  Typography,
  Grid,
} from '@mui/material';
import { Link } from 'react-router-dom';
import Navbar from './Navbar';
import '../Styles/Login.css';
import emailjs from "@emailjs/browser";
import Swal from 'sweetalert2';

emailjs.init('2PLXNvMdVdtdIuHKL');

function generateToken() {
  return Math.random().toString(36).substring(2);
}

const PwdResetForm = () => {
  const handleSubmit = (event) => {
    event.preventDefault();
    const email = event.target.email.value;
    console.log(email);
    const token = generateToken();
    emailjs.send('service_vtks8dh', 'template_x9h70pk', { to_email: email, link: `http://localhost:3000/resetPwdForm/${token}` })
      .then((response) => {
        console.log('SUCCESS!', response.status, response.text);
        Swal.fire("A Reset Password Link Sent to your Mail-ID", "Please reset your Password", "success");
      }, (err) => {
        console.log('FAILED...', err);
        Swal.fire("Invalid Email-ID", "Try Again", "error");
      });
  };

  return (
    <div>
      <Navbar />
      <Container maxWidth="sm" style={{ marginTop: '2rem', backgroundColor: "#f8f9fa", padding: "20px", borderRadius: "10px" }}>
        <div className="login-form">
          <Typography variant="h4" align="center" gutterBottom color='#1abc9c'>
            Welcome to SocialSphere
          </Typography>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  label="Email"
                  name="email"
                  type="email"
                  variant="outlined"
                  fullWidth
                  required
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  fullWidth
                >
                  SendLink
                </Button>
              </Grid>
              <Grid item xs={12}>
                <hr />
                <div className="text-center">
                  <Link to="/loginPage" style={{ marginRight: '10px' }}>
                    Login
                  </Link>
                  <Link to="/SignUpPage">Create New Account</Link>
                </div>
              </Grid>
            </Grid>
          </form>
        </div>
      </Container>
    </div>
  );
};

export default PwdResetForm;
