import React, { useState } from 'react';
import {Button, Box, Typography } from '@mui/material';
import '../Styles/newPost.css';
import UsersNavbar from './UsersNav';
import '../Styles/updatepf.css';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

const CreatePost = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    username: localStorage.getItem("Username") || '',
    caption: '',
    post: null,
  });

  const [fileName, setFileName] = useState('');

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handlePostChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      setFormData((prevData) => ({
        ...prevData,
        post: file,
      }));
      setFileName(file.name);
    } else {
      setFormData((prevData) => ({
        ...prevData,
        post: null,
      }));
      setFileName('');
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const dataToSend = new FormData();
    dataToSend.append('username', formData.username);
    dataToSend.append('caption', formData.caption);
    if (formData.post) {
      dataToSend.append('post', formData.post);
    }
    try {
      const response = await axios.post('http://localhost:8081/createPost', dataToSend, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
     if(response.data === "A new post is Created Successfully!"){
      Swal.fire("PostCreated Successfully","A new post is Created Successfully!","success");
      navigate('/myps');
     }else{
      Swal.fire("Error While Uploading","Please Try Again!","error");
      navigate('/newPost');
     }
    } catch (error) {
      console.error('Error uploading:', error);
      Swal.fire("Error While Uploading","Please Try Again!","error");
      navigate('/newPost');
    }
    
  };

  return (
    <div>
      <UsersNavbar />
      <div style={{ backgroundColor: "#f8f9fa", padding: "20px",width:"auto" }} className="crp-form">
        <Box component="form" onSubmit={handleSubmit} sx={{ mt: 3 }} className="post-form">
          <Typography variant="h6" gutterBottom textAlign={'center'} color="primary">
            Create a New Post
          </Typography>
          <div className="caps">
				<textarea   className="caption" name="caption" value={formData.caption}
					placeholder="Add Your Caption....." required onChange={handleChange} ></textarea>
			</div>
      <div style={{textAlign:"center",justifyContent:"center"}}>
          <Button
            variant="contained"
            component="label"
            sx={{ mb: 2,textTransform:"none" }}
          >
            Upload Image/Video
            <input
              type="file"
              name='post'
              required
              hidden
              accept="image/*,video/*"
              onChange={handlePostChange}
            />
          </Button>
          {fileName && (
        <Typography variant="body2" sx={{ mt: 1,padding:2 }}>
          Selected file: {fileName}
        </Typography>
      )}
          </div>
          <Box sx={{justifyContent:"center",textAlign:"center"}}>
          <Button type="submit" variant="contained" color="secondary" className='btn-update' style={{width:"90%"}}>
            CreatePost
          </Button>
          </Box>
        </Box>
      </div>
    </div>
  );
};

export default CreatePost;

