import React, { useState } from 'react';
import {
  Container,
  TextField,
  Button,
  Typography,
  Grid,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
} from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import '../Styles/signup.css';
import Navbar from './Navbar';
import Swal from 'sweetalert2';

const SignupForm = () => {
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    dob: '',
    gender: '',
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };
  const navigate = useNavigate();

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const response = await axios.post('http://localhost:8081/signUp', formData);
      console.log(response.data); 
      if(response.data === "User Added"){
        Swal.fire('User Added Successfully', 'A new User Added into Our DataBase', 'success');
        navigate('/loginPage')
      }else if(response.data === "User Exists"){
        Swal.fire('User Already Exists', 'A User found in our DataBase with the Credentials', 'error');
        navigate('/loginPage');
      }else{
        navigate('/loginPage');
      }
    } catch (error) {
      console.error('Error during signup:', error); 
    }
  };

  return (
    <div>
      <Navbar />
      <Container maxWidth="md" style={{ marginTop: '2rem', backgroundColor: "#f8f9fa", padding: "10px", borderRadius: "10px" }}>
        <div className="signup-form">
          <Typography variant="h4" align="center" gutterBottom color='#1abc9c'>
            Social Sphere
          </Typography>
          <Typography variant="h6" align="center" gutterBottom color='orange'>
            Create account on #1 Social media app!
          </Typography>
          <hr />
          <form onSubmit={handleSubmit}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  label="Username"
                  name="username"
                  variant="outlined"
                  fullWidth
                  required
                  value={formData.username}
                  onChange={handleChange}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Email"
                  name="email"
                  type="email"
                  variant="outlined"
                  fullWidth
                  required
                  value={formData.email}
                  onChange={handleChange}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Password"
                  name="password"
                  type="password"
                  variant="outlined"
                  fullWidth
                  required
                  value={formData.password}
                  onChange={handleChange}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Date of Birth"
                  name="dob"
                  type="date"
                  variant="outlined"
                  fullWidth
                  InputLabelProps={{
                    shrink: true,
                  }}
                  required
                  value={formData.dob}
                  onChange={handleChange}
                />
              </Grid>
              <Grid item xs={12}>
                <FormControl component="fieldset">
                  <FormLabel component="legend">Gender</FormLabel>
                  <RadioGroup row name="gender" value={formData.gender} onChange={handleChange} required>
                    <FormControlLabel value="Male" control={<Radio />} label="Male" />
                    <FormControlLabel value="Female" control={<Radio />} label="Female" />
                    <FormControlLabel value="Others" control={<Radio />} label="Others" />
                  </RadioGroup>
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  fullWidth
                >
                  SIGN UP
                </Button>
              </Grid>
              <Grid item xs={12}>
                <hr />
                <div className="text-center">
                  <Link to="/loginPage">Already have an account? Login instead</Link>
                </div>
              </Grid>
            </Grid>
          </form>
        </div>
      </Container>
    </div>
  );
};

export default SignupForm;
