import React, { useEffect} from 'react'
import UsersNavbar from './UsersNav';
import { Link } from 'react-router-dom';
import '../Styles/mypf.css';
import SocialSphere from '../Components/SocialSphere.png';
import { useDispatch, useSelector } from 'react-redux';
import { fetchUserDetails } from '../UsersStore/userActions';
import UserPosts from './UserPosts';

function MyProfile() {

  const dispatch = useDispatch();
    const userDetails = useSelector((state) => state.userDetails);

    useEffect(() => {
        const username = localStorage.getItem("Username") || '';
        if (username) {
            dispatch(fetchUserDetails(username));
        }
    }, [dispatch]);

  return (
    <div>
      <UsersNavbar />
      <div>
        <h1>My Profile</h1>
        <div  class="profile-container">
          <div class="profile-card">
            <div className='pfpd'>
            <img src={userDetails.profilePic ? `data:image/jpeg;base64,${userDetails.profilePic}` : SocialSphere } alt="ProfilePhoto" width="100px" height="100px" />
            <div class="bio">
              <b>Bio: </b><span>{userDetails.bio}</span>
            </div>
            </div>
            <div class="row">
              <div class="col">
                <b>Email: </b>
                <p>{userDetails.email}</p>
                <b>LinkedIn: </b>
                <p><Link to="{userDetails.linkdIn}">{userDetails.linkdIn}</Link></p>
                <b>GitHub: </b>
                <p><Link to="{userDetails.gitHub}">{userDetails.gitHub}</Link></p>
                <b>Date Of Birth: </b>
                <p>{userDetails.dob}</p>
              </div>
              <div class="col">
                <b>Current City: </b>
                <p>{userDetails.currentCity}</p>
                <b>College: </b>
                <p>{userDetails.college}</p>
                <b>Gender: </b>
                <p>{userDetails.gender}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <UserPosts />
    </div>
  );
}

export default MyProfile;
