import axios from 'axios';
import Swal from 'sweetalert2';

// Action Types
export const SET_USER_DETAILS = 'SET_USER_DETAILS';
export const SET_ALL_POSTS = 'SET_ALL_POSTS';
export const SET_USER_POSTS = 'SET_USER_POSTS';
export const SET_VISIT_PROFDETAILS = 'SET_VISIT_PROFDETAILS'; 
export const SET_VISIT_PROFILE_POSTS = 'SET_VISIT_PROFILE_POSTS'; 
export const SET_LIKES = 'SET_LIKES';
export const SET_COMMENTS = 'SET_COMMENTS';
export const RESET_PASSWORD = 'RESET_PASSWORD';

// Action Creator
export const setUserDetails = (userDetails) => ({
    type: SET_USER_DETAILS,
    payload: userDetails,
});

export const setAllPosts = (posts) => ({
    type: SET_ALL_POSTS,
    payload: posts,
});

export const setUserPosts = (userPosts) => ({
    type: SET_USER_POSTS,
    payload: userPosts,
});

export const setVisitProfDetails = (profileDetails) => ({
    type: SET_VISIT_PROFDETAILS,
    payload: profileDetails,
});

export const setVisitProfilePosts = (visitProfilePosts) => ({
    type: SET_VISIT_PROFILE_POSTS,
    payload: visitProfilePosts,
});

export const setLikes = (likes) =>({
    type:SET_LIKES,
    payload:likes,
});

export const setComments = (comments) =>({
    type:SET_LIKES,
    payload:comments,
});

export const resetPassword = (resetOrNot) =>({
    type:RESET_PASSWORD,
    payload:resetOrNot,
});

// Thunk to fetch user details
export const fetchUserDetails = (username) => {
    return async (dispatch) => {
        try {
            const resp = await axios.post('http://localhost:8081/getUserDetails', { username });
            dispatch(setUserDetails(resp.data)); 
        } catch (error) {
            console.error('Error fetching user details:', error);
        }
    };
};

// Thunk to fetch all posts
export const fetchAllPosts = () => {
    return async (dispatch) => {
        try {
            const resp = await axios.get('http://localhost:8081/getPosts');
            dispatch(setAllPosts(resp.data)); 
            console.log(resp.data);
        } catch (error) {
            console.error('Error fetching all posts:', error);
        }
    };
};

// Thunk to fetch posts for a specified user
export const fetchUserPosts = (username) => {
    return async (dispatch) => {
        try {
            const resp = await axios.get(`http://localhost:8081/getPosts/${username}`);
            dispatch(setUserPosts(resp.data)); 
        } catch (error) {
            console.error('Error fetching user posts:', error);
        }
    };
};

export const fetchVisitProfDetails = (username) => {
    return async (dispatch) => {
        try {
            const resp = await axios.get(`http://localhost:8081/getVisitorProfileDetails?username=${username}`);
            dispatch(setVisitProfDetails(resp.data));
            console.log(resp.data);
            
        } catch (error) {
            console.error('Error fetching visitor profile details:', error);
        }
    };
};

export const fetchVisitProfilePosts = (username) =>{
    return async (dispatch) => {
        try {
            const resp = await axios.get(`http://localhost:8081/getVisitorPosts?username=${username}`);
            console.log(resp.data);
            dispatch(setVisitProfilePosts(resp.data)); 
        } catch (error) {
            console.error('Error fetching user posts:', error);
        }
    };
}

export const fetchLikes = (id,likedProfileName) =>{
    return async (dispatch) => {
        try {
            const resp = await axios.get(`http://localhost:8081/likePost/${id}/${likedProfileName}`);
            console.log(resp.data);
            dispatch(setLikes(resp.data)); 
        } catch (error) {
            console.error('Error fetching user posts:', error);
        }
    };
}


export const fetchComments = (id,commentedProfileName, comment) =>{
    return async (dispatch) => {
        try {
            const resp = await axios.post(`http://localhost:8081/addComment/${id}/${commentedProfileName}/${comment}`);
            console.log(resp.data);
            dispatch(setComments(resp.data)); 
        } catch (error) {
            console.error('Error fetching user posts:', error);
        }
    };
}


export const  updatePassword = (email, password)=>{
    return async (dispatch) => {
        try {
            const resp = await axios.post(`http://localhost:8081/updatePassword/${email}/${password}`);
            console.log(resp.data);
            dispatch(resetPassword(resp.data));
            Swal.fire("Password Updated Successfully", 'Both password matched', "success"); 
        } catch (error) {
            console.error('Error fetching user posts:', error);
        }
    };
}

