import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { NavLink} from 'react-router-dom';
import { AppBar, Toolbar, Typography,   IconButton, Menu, MenuItem } from '@mui/material';
import '../Styles/NavbarStyles.css';
import SocialSphere from '../Components/SocialSphere.png';
import HomeIcon from '@mui/icons-material/Home';
import AddCardIcon from '@mui/icons-material/AddCard';
import PersonIcon from '@mui/icons-material/Person';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import EditIcon from '@mui/icons-material/Edit';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import { useSelector } from 'react-redux';




const pages = [
  { name: "Home", id: "homePage", path: "/usersHome",icon:<HomeIcon /> },
  { name: "CreateNewPost", id: "newPost", path: "/newPost",icon:<AddCardIcon /> },
  { name: "MyProfile", id: "MyProfile", path: "/mypf",icon:<PersonIcon /> },
  { name: "MyPosts", id: "posts", path: "/myps",icon:<FormatListBulletedIcon /> },
  { name: "UpdateProfile", id: "updateProfile", path: "/upf", icon:<EditIcon /> },
  { name: "Logout", id: "Logout", path: "/logout",icon:<ExitToAppIcon /> },
];

export default function UsersNav() {

  const [open, setOpen] = React.useState(false);

  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  const DrawerList = (
    <Box sx={{ width: 250 }} role="presentation" onClick={toggleDrawer(false)}>
      <List>
        {pages.map((page, index) => (
          <ListItem key={index} disablePadding>
            <ListItemButton>
              <ListItemIcon>
              <NavLink
                  to={page.path}
                  className={({ isActive }) => (isActive ? 'activatedNav' : 'mobnotActivatedNav')}
                  
                >
                  <Button color="inherit" startIcon={page.icon}>{page.name}</Button>
                </NavLink>
              </ListItemIcon>
              <ListItemText />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const userDetails = useSelector((state) => state.userDetails);
  
  return (
    <AppBar position="static" sx={{ backgroundColor: '#1a1a4b' }}>
      <Toolbar>
      <Button onClick={toggleDrawer(true)}><img src={SocialSphere} alt="Logo" style={{ height: '50px', cursor:"pointer",width:"50px", marginRight: '16px',borderRadius:"80%"}} /></Button>
      <Drawer open={open} onClose={toggleDrawer(false)}>
        {DrawerList}
      </Drawer>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          SocialSphere
        </Typography>
        <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
          {pages.map((page) => (
            <NavLink
              key={page.id}
              to={page.path}
              className={({ isActive }) => (isActive ? 'activatedNav' : 'notActivatedNav')}
            >
              <Button color="inherit">{page.name}</Button>
            </NavLink>
          ))}
        </Box>
        <IconButton onClick={handleClick} title='Settings'>
        <img src={userDetails.profilePic ? `data:image/jpeg;base64,${userDetails.profilePic}` : SocialSphere } alt="ProfilePhoto" style={{cursor:"pointer",height: '50px',width:"50px", marginRight: '16px',borderRadius:"80%"}} />
        <p style={{marginTop:"20px",color:"white",fontSize:"14px" }}>{userDetails.username}</p>
        </IconButton>
        <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
          <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose}>
            {pages.map((page) => (
              <MenuItem key={page.id} onClick={handleClose}>
                <NavLink
                  to={page.path}
                  className={({ isActive }) => (isActive ? 'activatedNav' : 'mobnotActivatedNav')}
                  
                >
                  <Button color="inherit" startIcon={page.icon}>{page.name}</Button>
                </NavLink>
              </MenuItem>
            ))}
          </Menu>
        </Box>
      </Toolbar>
    </AppBar>
  );
}





