import { SET_USER_DETAILS, SET_ALL_POSTS, SET_USER_POSTS, SET_VISIT_PROFDETAILS, SET_VISIT_PROFILE_POSTS, SET_LIKES, SET_COMMENTS, RESET_PASSWORD } from './userActions';

const initialState = {
    userDetails: {},
    allPosts: [],
    userPosts: [],
    visitProfileDetails: {},
    visitProfilePosts: [],
    likes:'',
    comments:[],
    resetOrNot:'',
};

const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DETAILS:
            return {
                ...state,
                userDetails: action.payload,
            };
        case SET_ALL_POSTS:
            return {
                ...state,
                allPosts: action.payload,
            };
        case SET_USER_POSTS:
            return {
                ...state,
                userPosts: action.payload,
            };
        case SET_VISIT_PROFDETAILS:
            return { ...state, visitProfileDetails: action.payload };
        case SET_VISIT_PROFILE_POSTS:
            return {...state, visitProfilePosts:action.payload};  
        case SET_LIKES:
            return {...state, likes:action.payload}; 
        case SET_COMMENTS:
            return {...state, comments:action.payload};  
        case RESET_PASSWORD:
            return {...state, resetOrNot:action.payload};           
        default:
            return state;
    }
};

export default userReducer;
